* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: 'Exo 2', sans-serif;
}
body {
    background:url(../../resources/bg.png) center (center/cover) round round ;
    display: flex;
    justify-content: center;
    align-items: center;
}

.app {
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    max-height: 100%;
    max-width: 1180px;
    width: 100%;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);   
    padding: 25px 0;  
}
.app__header{
    font-weight: 300;
    font-size: 36px;
    line-height: 42px;
    text-align: center;
    color: #FFFFFF;
    text-shadow: 0px 1px 1px #000000;
}
.github{
    font-weight: 300;
    font-size: 36px;
    line-height: 42px;
    text-shadow: 0px 1px 1px #000000;
    background: none;
    a{        
        color: #FFFFFF;
        background: none;
        text-decoration:underline;
        &:hover{
            color: #ddd4d4;
        }
    }
}
.container{  
    padding-top: 25px;  
    display: flex;
    align-items: center;
    justify-content: center;
    max-height: 100%;
    width: 100%;
    gap: 80px;
    flex-wrap: wrap;
    @media screen and (max-width:991.98px) {
        padding-bottom: 25px;
    }
}